.navbar{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin: 20px 17px;
}
.nav-Menu{
    display: flex;
    align-items: center;
    list-style: none;
    gap: 60px;
    font-size: 20px;

}
.nav-Connect{
    padding: 20px 40px;
    border-radius: 50px;
    background: linear-gradient(267deg, #DA7C25 0.36%, #B923E1 102.06%);
    font-size: 22px;
    cursor: pointer;
}

.nav-Connect:hover{
    transform: scale(1.05);
}

.nav-Menu li{
    display: flex;
    flex-direction: column;
    gap: 5px;
    cursor: pointer;
}

.nav-Menu li img{
    display: flex;
    margin: auto;
}

.anchor-link{
    text-decoration: none;
    color: white;
}

.nav-mob-close{
    display: none;
}

.nav-mob-open{
    display: none;
}

@media(max-width:768px){
    .navbar{
        margin: 20px 50px;
    }

    .nav-Connect{
        display: none;

    }

    .nav-mob-open{
        display: block;
        position: fixed;
        right: 30px;

    }

    .nav-mob-close{
        display: block;
        position: relative;
        top: 30px;
        left:290px;
        width: 30px;
    }

    .nav-Menu{
        position: fixed;
        flex-direction: column;
        align-items: start;
        top: 0;
        gap: 30px;
        background-color: #1f0016;
        width: 350px;
        height: 100%;
        z-index: 2;
        transition: right 0.5s;
        right: -350px;
    }

    .nav-Menu li{
        font-size: 30px;
        padding-left: 100px;
        flex-direction: row;
        gap: 20px;
    }
}