.mywork{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 80px;
    margin: 80px 170px;

}

.mywork-title{
    position: relative;

}

.mywork-title h1{
    padding: 0px 30px;
    font-size: 80px;
    font-weight: 600;
}

.mywork-title img{
    position: absolute;
    bottom: 0;
    right: 0;
    z-index: -1;
}

.mywork-container{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    gap: 40px;
}

.mywork-container img{
    box-sizing: border-box;
    width: 419px;
    height: 280px;
    transition: 0.3s;
    cursor: pointer;

}

.mywork-container img:hover{
    transform:scale(1.10);
    border: 4px solid #ff00ff;
    border-radius: 10px;
    transition: 0.3s;
}

.mywork-showmore{
    display: flex;
    justify-content: center;
    gap: 60px;
    padding: 20px 40px;
    border-radius: 50px;
    background: linear-gradient(267deg, #DA7C25 0.36%, #B923E1 102.06%);
    font-size: 22px;
    cursor: pointer;
}

.mywork-showmore:hover{
    transform: scale(1.05);
    transition: 0.6s;
}

@media(max-width:768px){

    .mywork{
        gap: 50px;
        margin: 50px 70px;
        align-items: start;

    }

    .mywork-title h1{
        padding: 0;
        font-size: 50px;
        font-weight: 600;
    }

    .mywork-title img{
        width: 130px;
        margin-right: -20px;

    }
    .mywork-container{
        grid-template-columns: 1fr 1fr 1fr;
        gap: 30px;
    }

    .mywork-container img{
        width: 100%;
        height: 100%;
    }

    .mywork-showmore{
        padding: 20px 40px;
        font-size: 18px;
        margin: 50px auto;
        cursor: pointer;
    }

}